<template>
  <div class="crime-events">
    <div class="page-header">
      <h1>犯罪事件管理</h1>
      <el-button type="primary" @click="showAddDialog">
        <el-icon><Plus /></el-icon>
        添加事件
      </el-button>
    </div>

    <!-- 搜索区域 -->
    <el-card class="search-card">
      <template #header>
        <span>搜索条件</span>
      </template>
      <el-form :model="searchForm" inline>
        <el-form-item label="犯罪类型">
          <el-select v-model="searchForm.crimeType" placeholder="选择犯罪类型" clearable>
            <el-option label="盗窃" value="盗窃" />
            <el-option label="抢劫" value="抢劫" />
            <el-option label="诈骗" value="诈骗" />
            <el-option label="暴力" value="暴力" />
            <el-option label="其他" value="其他" />
          </el-select>
        </el-form-item>
        <el-form-item label="事件状态">
          <el-select v-model="searchForm.status" placeholder="选择状态" clearable>
            <el-option label="待处理" value="待处理" />
            <el-option label="调查中" value="调查中" />
            <el-option label="已结案" value="已结案" />
            <el-option label="处理中" value="处理中" />
          </el-select>
        </el-form-item>
        <el-form-item label="严重程度">
          <el-select v-model="searchForm.severityLevel" placeholder="选择严重程度" clearable>
            <el-option label="轻微" value="轻微" />
            <el-option label="中等" value="中等" />
            <el-option label="严重" value="严重" />
          </el-select>
        </el-form-item>
        <el-form-item label="案发地点">
          <el-input v-model="searchForm.location" placeholder="输入地点关键词" />
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="searchEvents">
            <el-icon><Search /></el-icon>
            搜索
          </el-button>
          <el-button @click="resetSearch">
            <el-icon><Refresh /></el-icon>
            重置
          </el-button>
        </el-form-item>
      </el-form>
    </el-card>

    <!-- 事件列表 -->
    <el-card>
      <el-table :data="events" v-loading="loading" style="width: 100%">
        <el-table-column prop="id" label="ID" width="80" />
        <el-table-column prop="title" label="案件标题" width="200" />
        <el-table-column prop="crimeType" label="犯罪类型" width="100">
          <template #default="scope">
            <el-tag :type="getCrimeTypeTagType(scope.row.crimeType)">
              {{ scope.row.crimeType }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="location" label="案发地点" width="180" />
        <el-table-column prop="severityLevel" label="严重程度" width="100">
          <template #default="scope">
            <el-tag :type="getSeverityTagType(scope.row.severityLevel)">
              {{ scope.row.severityLevel }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column prop="incidentTime" label="案发时间" width="180">
          <template #default="scope">
            {{ formatDateTime(scope.row.incidentTime) }}
          </template>
        </el-table-column>
        <el-table-column prop="status" label="状态" width="100">
          <template #default="scope">
            <el-tag :type="getStatusTagType(scope.row.status)">
              {{ scope.row.status }}
            </el-tag>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="200" fixed="right">
          <template #default="scope">
            <el-button size="small" @click="viewEvent(scope.row)">
              <el-icon><View /></el-icon>
              查看
            </el-button>
            <el-button size="small" @click="editEvent(scope.row)">
              <el-icon><Edit /></el-icon>
              编辑
            </el-button>
            <el-button size="small" type="danger" @click="confirmDelete(scope.row)">
              <el-icon><Delete /></el-icon>
              删除
            </el-button>
          </template>
        </el-table-column>
      </el-table>

      <!-- 分页 -->
      <div class="pagination">
        <el-pagination
          v-model:current-page="currentPage"
          v-model:page-size="pageSize"
          :page-sizes="[10, 20, 50, 100]"
          :total="total"
          layout="total, sizes, prev, pager, next, jumper"
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
        />
      </div>
    </el-card>

    <!-- 添加/编辑事件对话框 -->
    <el-dialog
      v-model="dialogVisible"
      :title="isEdit ? '编辑犯罪事件' : '添加犯罪事件'"
      width="50%"
      :close-on-click-modal="false"
    >
      <el-form :model="form" :rules="rules" ref="formRef" label-width="100px">
        <el-form-item label="案件标题" prop="title">
          <el-input v-model="form.title" />
        </el-form-item>
        <el-form-item label="犯罪类型" prop="crimeType">
          <el-select v-model="form.crimeType" placeholder="选择犯罪类型" style="width: 100%">
            <el-option label="盗窃" value="盗窃" />
            <el-option label="抢劫" value="抢劫" />
            <el-option label="诈骗" value="诈骗" />
            <el-option label="暴力" value="暴力" />
            <el-option label="其他" value="其他" />
          </el-select>
        </el-form-item>
        <el-form-item label="案发地点" prop="location">
          <el-input v-model="form.location" />
        </el-form-item>
        <el-form-item label="案发时间" prop="incidentTime">
          <el-date-picker
            v-model="form.incidentTime"
            type="datetime"
            placeholder="选择案发时间"
            format="YYYY-MM-DD HH:mm:ss"
            value-format="YYYY-MM-DD HH:mm:ss"
            style="width: 100%"
          />
        </el-form-item>
        <el-form-item label="严重程度" prop="severityLevel">
          <el-select v-model="form.severityLevel" placeholder="选择严重程度" style="width: 100%">
            <el-option label="轻微" value="轻微" />
            <el-option label="中等" value="中等" />
            <el-option label="严重" value="严重" />
          </el-select>
        </el-form-item>
        <el-form-item label="事件状态" prop="status">
          <el-select v-model="form.status" placeholder="选择状态" style="width: 100%">
            <el-option label="待处理" value="待处理" />
            <el-option label="调查中" value="调查中" />
            <el-option label="处理中" value="处理中" />
            <el-option label="已结案" value="已结案" />
          </el-select>
        </el-form-item>
        <el-form-item label="事件描述" prop="description">
          <el-input
            v-model="form.description"
            type="textarea"
            :rows="4"
            placeholder="请输入事件详细描述"
          />
        </el-form-item>
      </el-form>
      <template #footer>
        <span class="dialog-footer">
          <el-button @click="dialogVisible = false">取消</el-button>
          <el-button type="primary" @click="saveEvent">确定</el-button>
        </span>
      </template>
    </el-dialog>

    <!-- 查看事件详情对话框 -->
    <el-dialog
      v-model="viewDialogVisible"
      title="事件详情"
      width="50%"
    >
      <el-descriptions :column="2" border>
        <el-descriptions-item label="案件ID">{{ currentEvent.id }}</el-descriptions-item>
        <el-descriptions-item label="案件标题">{{ currentEvent.title }}</el-descriptions-item>
        <el-descriptions-item label="犯罪类型">
          <el-tag :type="getCrimeTypeTagType(currentEvent.crimeType)">
            {{ currentEvent.crimeType }}
          </el-tag>
        </el-descriptions-item>
        <el-descriptions-item label="严重程度">
          <el-tag :type="getSeverityTagType(currentEvent.severityLevel)">
            {{ currentEvent.severityLevel }}
          </el-tag>
        </el-descriptions-item>
        <el-descriptions-item label="案发地点">{{ currentEvent.location }}</el-descriptions-item>
        <el-descriptions-item label="当前状态">
          <el-tag :type="getStatusTagType(currentEvent.status)">
            {{ currentEvent.status }}
          </el-tag>
        </el-descriptions-item>
        <el-descriptions-item label="案发时间">
          {{ formatDateTime(currentEvent.incidentTime) }}
        </el-descriptions-item>
        <el-descriptions-item label="上报时间">
          {{ formatDateTime(currentEvent.reportTime) }}
        </el-descriptions-item>
        <el-descriptions-item label="事件描述" :span="2">
          {{ currentEvent.description || '无描述' }}
        </el-descriptions-item>
      </el-descriptions>
    </el-dialog>
  </div>
</template>

<script>
import { ElMessage, ElMessageBox } from 'element-plus'
import { Plus, Search, Refresh, View, Edit, Delete } from '@element-plus/icons-vue'
import crimeEventService from '../services/crimeEventService'

export default {
  name: 'CrimeEvents',
  components: {
    Plus, Search, Refresh, View, Edit, Delete
  },
  data() {
    return {
      events: [],
      loading: false,
      currentPage: 1,
      pageSize: 10,
      total: 0,
      dialogVisible: false,
      viewDialogVisible: false,
      isEdit: false,
      currentEvent: {},
      searchForm: {
        crimeType: '',
        status: '',
        severityLevel: '',
        location: ''
      },
      form: {
        id: null,
        title: '',
        crimeType: '',
        location: '',
        description: '',
        incidentTime: '',
        status: '待处理',
        severityLevel: ''
      },
      rules: {
        title: [
          { required: true, message: '请输入案件标题', trigger: 'blur' },
          { min: 5, max: 200, message: '标题长度在 5 到 200 个字符', trigger: 'blur' }
        ],
        crimeType: [
          { required: true, message: '请选择犯罪类型', trigger: 'change' }
        ],
        location: [
          { required: true, message: '请输入案发地点', trigger: 'blur' }
        ],
        incidentTime: [
          { required: true, message: '请选择案发时间', trigger: 'change' }
        ],
        severityLevel: [
          { required: true, message: '请选择严重程度', trigger: 'change' }
        ],
        status: [
          { required: true, message: '请选择事件状态', trigger: 'change' }
        ]
      }
    }
  },
  created() {
    this.fetchEvents()
  },
  methods: {
    async fetchEvents() {
      this.loading = true
      try {
        const response = await crimeEventService.getCrimeEventsPage(this.currentPage - 1, this.pageSize)
        this.events = response.data.content
        this.total = response.data.totalElements
      } catch (error) {
        ElMessage.error('获取犯罪事件列表失败')
        console.error('Error fetching crime events:', error)
      } finally {
        this.loading = false
      }
    },

    async searchEvents() {
      this.loading = true
      try {
        const response = await crimeEventService.searchCrimeEvents(this.searchForm)
        this.events = response.data
        this.total = response.data.length
      } catch (error) {
        ElMessage.error('搜索失败')
        console.error('Error searching events:', error)
      } finally {
        this.loading = false
      }
    },

    resetSearch() {
      this.searchForm = {
        crimeType: '',
        status: '',
        severityLevel: '',
        location: ''
      }
      this.fetchEvents()
    },

    showAddDialog() {
      this.isEdit = false
      this.form = {
        id: null,
        title: '',
        crimeType: '',
        location: '',
        description: '',
        incidentTime: '',
        status: '待处理',
        severityLevel: ''
      }
      this.dialogVisible = true
    },

    viewEvent(event) {
      this.currentEvent = { ...event }
      this.viewDialogVisible = true
    },

    editEvent(event) {
      this.isEdit = true
      this.form = {
        ...event,
        incidentTime: this.formatDateTimeForInput(event.incidentTime)
      }
      this.dialogVisible = true
    },

    async saveEvent() {
      try {
        await this.$refs.formRef.validate()

        if (this.isEdit) {
          await crimeEventService.updateCrimeEvent(this.form.id, this.form)
          ElMessage.success('犯罪事件更新成功')
        } else {
          await crimeEventService.createCrimeEvent(this.form)
          ElMessage.success('犯罪事件创建成功')
        }

        this.dialogVisible = false
        this.fetchEvents()
      } catch (error) {
        if (error.response && error.response.data) {
          ElMessage.error(error.response.data.message || '操作失败')
        } else {
          ElMessage.error('操作失败')
        }
        console.error('Error saving event:', error)
      }
    },

    confirmDelete(event) {
      ElMessageBox.confirm(
        `确定要删除案件 "${event.title}" 吗？`,
        '警告',
        {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }
      ).then(async () => {
        try {
          await crimeEventService.deleteCrimeEvent(event.id)
          ElMessage.success('删除成功')
          this.fetchEvents()
        } catch (error) {
          ElMessage.error('删除失败')
          console.error('Error deleting event:', error)
        }
      }).catch(() => {
        // 用户取消删除
      })
    },

    handleSizeChange(newSize) {
      this.pageSize = newSize
      this.fetchEvents()
    },

    handleCurrentChange(newPage) {
      this.currentPage = newPage
      this.fetchEvents()
    },

    getCrimeTypeTagType(crimeType) {
      const typeMap = {
        '盗窃': 'warning',
        '抢劫': 'danger',
        '诈骗': 'info',
        '暴力': 'danger',
        '其他': ''
      }
      return typeMap[crimeType] || ''
    },

    getSeverityTagType(severity) {
      const severityMap = {
        '轻微': 'success',
        '中等': 'warning',
        '严重': 'danger'
      }
      return severityMap[severity] || ''
    },

    getStatusTagType(status) {
      const statusMap = {
        '待处理': 'info',
        '调查中': 'warning',
        '处理中': 'warning',
        '已结案': 'success'
      }
      return statusMap[status] || ''
    },

    formatDateTime(dateString) {
      if (!dateString) return ''
      const date = new Date(dateString)
      return date.toLocaleString('zh-CN')
    },

    formatDateTimeForInput(dateString) {
      if (!dateString) return ''
      const date = new Date(dateString)
      return date.toISOString().slice(0, 19).replace('T', ' ')
    }
  }
}
</script>

<style scoped>
.crime-events {
  padding: 1rem;
}

.page-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 2rem;
}

.page-header h1 {
  color: #2c3e50;
}

.search-card {
  margin-bottom: 2rem;
}

.pagination {
  margin-top: 1rem;
  text-align: center;
}
</style>